import { keepPreviousData, useQuery } from "@tanstack/react-query"
import { AUTOSUGGEST_SOURCES_INPUT_INFINITE_PAGE_SIZE } from "../content.constants"
import { getAutosuggestSources } from "../services/getAutosuggestSources"
import { autosuggestSourcesInputQueryKeys } from "../utils/autosuggestSourcesInputQueryKeys"

type UseAutosuggestSourcesInputParams = {
  query: string
}
function useAutosuggestSourcesInput({ query }: UseAutosuggestSourcesInputParams) {
  return useQuery({
    enabled: !!query,
    queryKey: autosuggestSourcesInputQueryKeys.autosuggestInput(query),
    queryFn: () =>
      getAutosuggestSources({
        pageCount: AUTOSUGGEST_SOURCES_INPUT_INFINITE_PAGE_SIZE,
        query,
      }),
    staleTime: 60 * 1000,
    refetchOnWindowFocus: false,
    placeholderData: keepPreviousData,
  })
}

export { useAutosuggestSourcesInput }
